import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {
  MatToolbarModule,
  MatIconModule,
  MatButtonModule,
  MatTooltipModule,
  MatListModule,
  MatFormFieldModule,
  MatInputModule,
  MatSelectModule,
  MatProgressSpinnerModule,
  MatProgressBarModule,
  MatCardModule,
  MatSnackBarModule,
  MatButtonToggleModule,
  MatSlideToggleModule,
  MatTableModule,
  MatPaginatorModule,
  MatSortModule,
  MatMenuModule,
  MatDialogModule,
  MatChipsModule,
  MatSidenavModule,
  MatExpansionModule,
  MatTabsModule,
  MatCheckboxModule,
  MatRadioModule
} from '@angular/material';
import { AppComponent } from './app.component';

// SERVICES
import { InteractionService } from './services/interaction.service';
import { UserSettingsService } from './services/user-settings.service';
import { UtilsService } from './services/utils.service';
import { MapsService } from './services/maps.service';
import { Ndo6Service } from './services/ndo6.service';

// COMPONENTS
import { ConfirmDialogComponent } from './components/confirm-dialog/confirm-dialog.component';
import {
  OverpagesComponent,
  OverpageSettingsComponent
} from './components/overpages/overpages.component';


@NgModule({
  declarations: [
    AppComponent,
    ConfirmDialogComponent,
    OverpagesComponent,
    OverpageSettingsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    MatToolbarModule,
    MatIconModule,
    MatButtonModule,
    MatTooltipModule,
    MatListModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatProgressSpinnerModule,
    MatProgressBarModule,
    MatCardModule,
    MatSnackBarModule,
    MatButtonToggleModule,
    MatSlideToggleModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatMenuModule,
    MatDialogModule,
    MatChipsModule,
    MatSidenavModule,
    MatExpansionModule,
    MatTabsModule,
    MatCheckboxModule,
    MatRadioModule
  ],
  providers: [
    UtilsService,
    UserSettingsService,
    InteractionService,
    MapsService,
    Ndo6Service
  ],
  entryComponents: [
    ConfirmDialogComponent
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }



