import { Injectable } from '@angular/core';
import {MapsService} from './maps.service';
import {UserSettingsService} from './user-settings.service';
import {UtilsService} from './utils.service';
import _ from 'lodash';
declare const google: any;



export class Position {
  id: string;
  title: string;
  label: string;
  type: string;
  last = false;
  latitude: any = null;
  longitude: any = null;
  altitude: any = null;
  accuracy: any = null;
  altitudeAccuracy: any = null;
  heading: any = null;
  speed: any = null;
  timestamp: any = null;
  marker: any = null;
  constructor(public info: any = null, public data: any = null) {
    this.keep(info);
    this.id = data ? data.id : '';
    this.title = data ? data.title : '';
    this.label = data ? data.label : '';
    this.type = data ? data.type : '';
  }
  isValid() {
    return (this.latitude && this.longitude);
  }
  getLatLng() {
    return new google.maps.LatLng(this.latitude, this.longitude);
  }
  sameOf(pos) {
    return pos &&
      pos.latitude === this.latitude &&
      pos.longitude === this.longitude &&
      pos.altitude === this.altitude;
  }
  keep(pos) {
    if (pos) {
      this.id = pos.id;
      this.title = pos.title;
      this.label = pos.label;
      this.type = pos.type;
      if (pos.timestamp) { this.timestamp = pos.timestamp; }
      if (pos.coords) { pos = pos.coords; }
      this.latitude = pos.latitude;
      this.longitude = pos.longitude;
      this.altitude = pos.altitude;
      this.accuracy = pos.accuracy;
      this.altitudeAccuracy = pos.altitudeAccuracy;
      this.heading = pos.heading;
      this.speed = pos.speed;
    }
  }
  dispose() {
    if (this.marker) {
      this.marker.setMap(null);
    }
  }
}



@Injectable()
export class Ndo6Service {

  constructor(private maps: MapsService,
              private user: UserSettingsService,
              private u: UtilsService) { }
  private positionChecker = null;
  last: Position = null;
  session: any = {
    user: {},
    map: null,
    context: null
  };
  active = false;
  posErrorCounter = 0;
  clickOnMarker = null;
  setZoom(zoom) {
    const self = this;
    if (!zoom || !self.session.context) { return; }
    const listener = google.maps.event.addListener(self.session.context.map, 'idle', () => {
      self.session.context.map.setZoom(zoom);
      google.maps.event.removeListener(listener);
    });
  }
  /**
   * centra la mappa
   */
  centerMap(pos, zoom, finder) {
    const self = this;
    if (!self.session.context || !pos) { return; }
    // il centro è considerato più in alto per
    // lasciare lo spazio al monitor
    const bounds = self.session.context.map.getBounds();
    if (!bounds) { return; }

    // Calcola le coordinate del centro
    const gpos = self.maps.getLatLng(pos);

    // Imposta il centro della mappa
    self.session.context.map.setCenter(gpos);

    const mrk = finder ? finder() : null;
    if (mrk) {
      // Se ha trovato il marker lo anima
      mrk.setAnimation(google.maps.Animation.BOUNCE);
      setTimeout(function () {
        mrk.setAnimation(null);
      }, 1000);
    }
    self.setZoom(zoom);
  }
  checkGeo() {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        return reject('Geolocation is not supported by this browser.');
      } else {
        navigator.geolocation.getCurrentPosition(resolve, function (err) {
          console.error(err);
          reject('Geolocation is not available.');
        });
      }
    });
  }
  reset() {
    if (this.positionChecker) {
      clearTimeout(this.positionChecker);
    }
    this.active = false;
    this.clickOnMarker = null;
  }
  setPos(pos) {
    const self = this;
    const nick = (self.session.user.nick || 'self');
    const npos = new Position(pos, {
      id: 'user@' + nick,
      title: nick,
      label: nick.slice(0, 1),
      type: 'user'
    });
    self.last = self.last || new Position();
    if (!self.last.marker) {
      self.last.marker = self.getMarker(self.last);
    }
    if (!self.last.sameOf(npos) && npos.isValid()) {
      const latlng = npos.getLatLng();
      self.last.marker.setPosition(latlng);
    }
  }
  private checkPos() {
    const self = this;
    self.positionChecker = setTimeout(() => {
      self.checkGeo()
        .then((pos) => {
          self.setPos(pos);
          if (self.active) {
            self.checkPos();
          }
        }, err => {
          console.error(err);
          self.posErrorCounter++;
          if (self.posErrorCounter < 4) {
            self.checkPos();
          }
        });
    }, self.user.settings.delay || 1000);
  }
  activate(context) {
    const self = this;
    self.session.context = context;
    if (self.positionChecker) {
      clearTimeout(self.positionChecker);
    }
    self.checkPos();
    self.active = true;
  }
  getMarkerIcon(url) {
    if (url) {
      return {
        url: url,
        size: new google.maps.Size(22, 40),
        origin: new google.maps.Point(0, 0),
        anchor: new google.maps.Point(11, 40)
      };
    }
  }
  getMarker(info) {
    const self = this;
    if (!self.session.context) { return null; }
    const latlnt = self.maps.getLatLng(info);
    const m = new google.maps.Marker({
      map: self.session.context.map,
      label: info.label || 'P',
      position: latlnt,
      title: info.title || info.description,
      icon: self.getMarkerIcon(info.icon)
    });
    m.ndo6 = {
      id: self.u.guid()
    };
    _.extend(m.ndo6, info);
    google.maps.event.addListener(m, 'click', function() {
      if (_.isFunction(self.clickOnMarker)) {
        self.clickOnMarker(m);
      }
    });
    return m;
  }

}



