import { Component, OnInit } from '@angular/core';
import { environment } from '../environments/environment';
import { fadeAnimation } from './app.animations';
import {InteractionService} from './services/interaction.service';
import {UtilsService} from './services/utils.service';
import {MapsService} from './services/maps.service';
import {Ndo6Service} from "./services/ndo6.service";


const GOOGLE_MAP_URL = 'https://maps.googleapis.com/maps/api/js?key={URL}&libraries=geometry,places&callback={CALLBACK}';
const GOOGLE_CALLBACK_NAME = 'googleMapsInitialized';
const initializer = {
  run: (info) => {
    return new Promise((resolve) => {
      window[GOOGLE_CALLBACK_NAME] = resolve;
      let url = GOOGLE_MAP_URL.replace(/\{URL\}/g, info.googleKey);
      url = url.replace(/\{CALLBACK\}/g, GOOGLE_CALLBACK_NAME);
      console.log('URL: %s', url);
      const script = document.createElement('script');
      script.src = url;
      document.body.appendChild(script);
    });
  }
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  animations: [fadeAnimation]
})
export class AppComponent implements OnInit {
  constructor(private interaction: InteractionService,
              public u: UtilsService,
              private maps: MapsService,
              private ndo6: Ndo6Service) {
    // const self = this;
  }
  loading = true;
  info: any = {};
  // info_str = '';
  debug = !environment.production;
  refresh() {
    const self = this;
    self.u.err = null;
    self.info = {};
    self.ndo6.reset();
    self.interaction.getInfo().subscribe(data => {
      self.info = data;
      initializer.run(data).then(() => {
        self.maps.create((ctx) => {
          self.ndo6.clickOnMarker = self.clickOnMarker;
          self.ndo6.activate(ctx);
        });
        self.loading = false;
      }, err => {
        self.u.error(err);
        self.loading = false;
      });
    }, err => {
      self.u.error(err);
      self.loading = false;
    });
  }
  ngOnInit() {
    this.refresh();
  }
  hideError() {
    this.u.err = null;
  }
  settings() {
    this.u.overpage.type = 'settings';
  }
  clickOnMarker(m) {
    console.log('Click on marker: ', m);
  }
}




